import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router'; 
import { ListaGridComponent } from './paginas/lista/lista-grid/lista-grid.component';
import { ListaComponent } from './paginas/lista/lista-usuarios/lista.component';
import { Pag1Component } from './paginas/w3c/pag1/pag1.component';

const routes: Routes = [
  { path: 'lista/grid', component: ListaComponent } ,
  { path: '', component: ListaGridComponent } ,
  { path: 'w3c/1', component: Pag1Component } ,
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
